@import url(https://fonts.googleapis.com/css2?family=Courier+Prime&display=swap);
/* .cart{
    background-color: aqua;
} */
.set-width {
    width: 25vw;
}

.cart {
    border-left: 3px solid var(--dark);

    height: 75vh;

}



.books {
    overflow: scroll;
    overflow-x: hidden;
    height: 50vh;
    padding: 10px;
    border: 1px solid var(--dark);
    border-radius: 10px;
    /* height:100%; */

}

.cartInfo {
    margin:0 auto;
}

.cartItem {}

.cartImg {
    width: 100%;
}

/* //////////// */

.itemImg {
    flex: 1 1;
    padding: 10px;
}

.itemInfo {
    flex: 4 1;
    padding: 10px;
}

input {
    width: 20%;
    border: none;
    background: none;
}

::-webkit-scrollbar {
    width: 7px;
}

::-webkit-scrollbar-track {
    border: 1px solid var(--dark);
    border-radius: 10px;
}

::-webkit-scrollbar-thumb {
    background: var(--dark);
    border-radius: 10px;
    
}

::-webkit-scrollbar-button {
    visibility: hidden;
}
@font-face {
    font-family: Grotesk;
    src: url(/static/media/Grotesk.c13ab066.otf) format("opentype");
}

.logo {
    font-family: Grotesk;
    font-size: 200%;
}

a {
    text-decoration: none;
    color: var(--dark);
}

.filter-btn {
    border: none;
    background: none;
    color:var(--dark);
}

.filter-btn:focus {
    border: none;
    outline: none;
    color: var(--primary);
}

.filter-btn:focus{
    outline:none !important;
}

.spacing {
    padding: 1px 6px;
}

ul {
    list-style: none;
}

li {
    margin-bottom: 8px;
}

.under::after {
    content: "";
    display: block;
    width: 80px;
    height: 2px;
    background: var(--dark);
    bottom: 10px;
    left: 0;
}
@font-face {
    font-family: Grotesk, sans-serif;
    src: url(/static/media/Grotesk.c13ab066.otf);
}

:root {
    --primary: #723636;
    --background: #F4E7DA;
    --dark: #232323;
    --light: #fafafa;
    --error: rgb(228, 46, 1);
    /* --spacing-one: calc(1rem * 0.5);
    --spacing-two: calc(1rem); */
}

* {
    box-sizing: border-box;
}

/* ========================================================================================== */

html,
body {
    min-height: 100%;

}

body {
    margin: 0;
    padding: 0;
    font-weight: 400;
    line-height: 1.5;
    font-size: 16px;
    color: #232323;
    color: var(--dark);
}

.page-bg {
    background-color: #F4E7DA;
    background-color: var(--background);
    width: 100vw;
    height: 100vh;
    font-family: 'Courier Prime', monospace;
}

a {
    text-decoration: none;
    color: #232323;
    color: var(--dark);
}

/* ========================================================================================== */


.about {
    border-top: 3px solid #232323;
    border-top: 3px solid var(--dark);
    padding:10px;
}

.side-menu {
    height: 100vh;
    border-right: 3px solid #232323;
    border-right: 3px solid var(--dark);
}

.main-page {
    /* width: 100vw;
    height: 100vh; */
}

.bookBorder>img{
object-fit:cover;
width:100%;

}

.bookBorder{
    border:2px solid #232323;
    border:2px solid var(--dark);
    border-radius: 2px;
    flex: 0 1 a;
    padding: 30px;
    position: relative;
}



.book-list{
    grid-gap: 10px 20px;
    gap: 10px 20px;
    overflow: scroll;
   height: 75vh;
    overflow-x: hidden; 
    padding: 10px;
}

::-webkit-scrollbar {
    width: 7px;
  }

  ::-webkit-scrollbar-track {
    border: 1px solid #232323;
    border: 1px solid var(--dark);
    border-radius: 10px;
  }

  ::-webkit-scrollbar-thumb {
    background: #232323;
    background: var(--dark);
    border-radius: 10px;
  }

  ::-webkit-scrollbar-button{
      visibility: hidden;
  }



.button{
      background-color: #232323;
      color:#F4E7DA;
      color:var(--background);
      border-radius: 10px;
      margin:10px;
      font-size:90%;
      font-family: Grotesk;
      border: 0;
      padding: 0.25rem 0.5rem;
     }

     .bookItem>button:hover{
        background-color: #723636;
        background-color: var(--primary);
       }

  .info{
    /* display:none; */
    visibility: hidden;
    position: absolute;
    top: 0%;
    right: 0%;
    width:100%;
    height:100%;
    background:#723636be;
  }

  .price{
    margin: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    color:#fafafa;
    color:var(--light);
    text-shadow: 2px 2px #232323;
    text-shadow: 2px 2px var(--dark);
    font-family: Grotesk;
  }

 .bookBorder:hover >.info{
    /* display:block; */
    visibility: visible;
    
  }

.singleBorder>img{
    object-fit:cover;
    width:100%;
}

.coverImg{
    border:2px solid #232323;
    border:2px solid var(--dark);
    border-radius: 2px;
    padding:20%;
}
